import React, { useContext } from "react";
import { AccountsContext, GridBotsContext } from "../../../../App";
import { useTable } from "react-table";
import { Badge, OverlayTrigger, Table, Tooltip } from "react-bootstrap";

import "./GridBotsListTable.css";
import { getCurrencyFormat } from "../../../../utils/BotsUtils";
import CurrencyIcon from "../../../CommonComponents/CurrencyIcon/CurrencyIcon";
import _ from "lodash";

const GridBotsListTable = ({ values, investment, setModalGridBotId }: any) => {
  const { gridBotsState } = useContext(GridBotsContext);
  const { list, gridBots } = gridBotsState;
  const {
    accountsState: { accounts, list: accountsList },
  } = useContext(AccountsContext);

  const columns = React.useMemo(
    () =>
      [
        {
          Header: "Account",
          id: "account",
          accessor: (originalRow: any, rowIndex: any) => {
            return originalRow.account_id;
          },
          disableSortBy: true,
          Cell: ({ cell: { value } }: any) => {
            const accountId = accountsList.filter(
              (accId: any) => accId === value
            )[0];
            return (
              <div style={{ display: "flex" }}>
                <div
                  className="AccountMarketIcon"
                  style={{
                    backgroundImage: `url(${accounts[accountId].market_icon})`,
                  }}
                ></div>
                <div style={{ width: "100px" }}>{accounts[accountId].name}</div>
              </div>
            );
          },
        },

        {
          Header: "Pair",
          Cell: ({ cell: { value } }: any) => {
            const baseCurrency = value.split("_")[1];

            return (
              <div style={{ display: "flex" }}>
                <CurrencyIcon currency={baseCurrency} />
                <div style={{ marginLeft: "4px" }}>
                  {value.split("_").reverse().join("/")}
                </div>
              </div>
            );
          },
          accessor: "pair",
          disableSortBy: true,
        },
        {
          Header: "Price range",
          accessor: (originalRow: any, rowIndex: any) => {
            return `${originalRow.upper_price}_${originalRow.lower_price}_${originalRow.grids_quantity} levels`;
          },
          id: "price_range",
          disableSortBy: true,
          Cell: ({ cell: { value } }: any) => {
            const range = value.split("_");
            return (
              <div>
                <div
                  style={{
                    color: "#00A59A",
                  }}
                >
                  {range[0]}
                </div>
                <div
                  style={{
                    color: "#EE7D8B",
                  }}
                >
                  {range[1]}
                </div>
                <div
                  style={{
                    fontSize: "12px",
                  }}
                >
                  {range[2]}
                </div>
              </div>
            );
          },
        },
        {
          Header: "Start price",
          accessor: "start_price",
          disableSortBy: true,
        },
        {
          Header: "Current price",
          accessor: "current_price",
          disableSortBy: true,
        },
        {
          Header: "Active orders",
          accessor: (originalRow: any, rowIndex: any) => {
            const grigLinesNumber = originalRow.grid_lines.length;
            const sellOrdersNumber = originalRow.grid_lines.filter(
              (line: any) => line.order_placed && line.side === "sell"
            ).length;
            const buyOrdersNumber = originalRow.grid_lines.filter(
              (line: any) => line.order_placed && line.side === "buy"
            ).length;

            return (
              <>
                <div style={{ marginBottom: "3px" }}>
                  <Badge style={{ fontSize: "14px" }} bg="danger">
                    {sellOrdersNumber}
                  </Badge>
                </div>
                <div style={{ marginBottom: "3px" }}>
                  <Badge style={{ fontSize: "14px" }} bg="success">
                    {buyOrdersNumber}
                  </Badge>
                </div>
                {sellOrdersNumber + buyOrdersNumber !== grigLinesNumber - 1 && (
                  <div style={{ marginBottom: "3px" }}>
                    <OverlayTrigger
                      key="right"
                      placement="right"
                      overlay={
                        <Tooltip id={`tooltip-${originalRow.id}`}>
                          Bot was unable to place all orders on the exchange.
                          This may heavily impact your trading results and PnL
                          reports on this page. Please contact 3commas support
                          to check the error details.
                        </Tooltip>
                      }
                    >
                      <Badge style={{ fontSize: "16px" }} bg="warning">
                        {"\u26A0"}
                      </Badge>
                    </OverlayTrigger>
                  </div>
                )}
              </>
            );
          },
          id: "activeOrder",
          disableSortBy: true,
        },
        {
          Header: "Initial investment",
          accessor: (originalRow: any, rowIndex: any) => {
            const quoteCurrency = originalRow.pair.split("_")[0];
            const baseCurrency = originalRow.pair.split("_")[1];

            if (!originalRow.initialInvestments) {
              return "";
            } else if (investment === "balances") {
              return (
                <>
                  <div style={{ display: "flex" }}>
                    <CurrencyIcon currency={baseCurrency} />
                    <div style={{ marginLeft: "4px" }}>{`${getCurrencyFormat({
                      value: originalRow.initialInvestments.baseInvestment,
                      currency: baseCurrency,
                    })} ${baseCurrency}`}</div>
                  </div>
                  <div style={{ display: "flex" }}>
                    <CurrencyIcon currency={quoteCurrency} />
                    <div style={{ marginLeft: "4px" }}>{`${getCurrencyFormat({
                      value: originalRow.initialInvestments.quoteInvestment,
                      currency: quoteCurrency,
                    })} ${quoteCurrency}`}</div>
                  </div>
                </>
              );
            } else {
              const cur = values === "usd" ? "USD" : quoteCurrency;
              const val =
                values === "usd"
                  ? originalRow.initialInvestments.allInUsd
                  : originalRow.initialInvestments.allInQuote;
              return (
                <div style={{ display: "flex" }}>
                  <CurrencyIcon currency={cur} />
                  <div style={{ marginLeft: "4px" }}>{`${getCurrencyFormat({
                    value: val,
                    currency: cur,
                  })} ${cur}`}</div>
                </div>
              );
            }
          },
          id: "initial_investment",
          disableSortBy: true,
        },
        {
          Header: "Current investment",
          accessor: (originalRow: any, rowIndex: any) => {
            const quoteCurrency = originalRow.pair.split("_")[0];
            const baseCurrency = originalRow.pair.split("_")[1];

            if (!originalRow.currentInvestments) {
              return "";
            } else if (investment === "balances") {
              return (
                <>
                  <div style={{ display: "flex" }}>
                    <CurrencyIcon currency={baseCurrency} />
                    <div style={{ marginLeft: "4px" }}>{`${getCurrencyFormat({
                      value: originalRow.currentInvestments.baseInvestment,
                      currency: baseCurrency,
                    })} ${baseCurrency}`}</div>
                  </div>
                  <div style={{ display: "flex" }}>
                    <CurrencyIcon currency={quoteCurrency} />
                    <div style={{ marginLeft: "4px" }}>{`${getCurrencyFormat({
                      value: originalRow.currentInvestments.quoteInvestment,
                      currency: quoteCurrency,
                    })} ${quoteCurrency}`}</div>
                  </div>
                </>
              );
            } else {
              const cur = values === "usd" ? "USD" : quoteCurrency;
              const val =
                values === "usd"
                  ? originalRow.currentInvestments.allInUsd
                  : originalRow.currentInvestments.allInQuote;
              return (
                <div style={{ display: "flex" }}>
                  <CurrencyIcon currency={cur} />
                  <div style={{ marginLeft: "4px" }}>{`${getCurrencyFormat({
                    value: val,
                    currency: cur,
                  })} ${cur}`}</div>
                </div>
              );
            }
          },
          id: "current_investment",
          disableSortBy: true,
        },
        {
          Header: "Investment change",
          accessor: (originalRow: any, rowIndex: any) => {
            const quoteCurrency = originalRow.pair.split("_")[0];

            if (
              !originalRow.currentInvestments ||
              !originalRow.initialInvestments
            ) {
              return "";
            } else if (investment === "balances" || values === "quote") {
              const invesmentChange =
                originalRow.currentInvestments.allInQuote -
                originalRow.initialInvestments.allInQuote;
              return (
                <>
                  <div>
                    <div>{`${getCurrencyFormat({
                      value: invesmentChange,
                      currency: quoteCurrency,
                    })} ${quoteCurrency}`}</div>
                  </div>
                  <div
                    className={
                      invesmentChange >= 0 ? "PositiveNumber" : "NegativeNumber"
                    }
                  >{`${getCurrencyFormat({
                    value: originalRow.initialInvestments.allInQuote
                      ? (invesmentChange /
                          originalRow.initialInvestments.allInQuote) *
                        100
                      : 0,
                    currency: "USD",
                  })} %`}</div>
                </>
              );
            } else {
              const invesmentChange =
                originalRow.currentInvestments.allInUsd -
                originalRow.initialInvestments.allInUsd;
              return (
                <>
                  <div>
                    <div>{`${getCurrencyFormat({
                      value: invesmentChange,
                      currency: "USD",
                    })} USD`}</div>
                  </div>
                  <div
                    className={
                      invesmentChange >= 0 ? "PositiveNumber" : "NegativeNumber"
                    }
                  >{`${getCurrencyFormat({
                    value: originalRow.initialInvestments.allInUsd
                      ? (invesmentChange /
                          originalRow.initialInvestments.allInUsd) *
                        100
                      : 0,
                    currency: "USD",
                  })} %`}</div>
                </>
              );
            }
          },
          id: "investment_change",
          disableSortBy: true,
        },
        {
          Header: "Bot profit",
          accessor: (originalRow: any, rowIndex: any) => {
            const quoteCurrency = originalRow.pair.split("_")[0];

            if (!originalRow.profits) {
              return "";
            } else if (investment === "balances" || values === "quote") {
              const quoteProfit = _.sum(
                originalRow.profits.map((profit: any) =>
                  parseFloat(profit.profit)
                )
              );
              const profitPercent =
                (quoteProfit / originalRow.initialInvestments.allInQuote) * 100;
              return (
                <>
                  <div>{`${getCurrencyFormat({
                    value: quoteProfit,
                    currency: quoteCurrency,
                  })} ${quoteCurrency}`}</div>
                  <div
                    className={
                      profitPercent >= 0 ? "PositiveNumber" : "NegativeNumber"
                    }
                  >{`${getCurrencyFormat({
                    value: profitPercent,
                    currency: "USD",
                  })} %`}</div>
                </>
              );
            } else {
              const usdProfit = _.sum(
                originalRow.profits.map((profit: any) =>
                  parseFloat(profit.usd_profit)
                )
              );
              const profitPercent =
                (usdProfit / originalRow.initialInvestments.allInUsd) * 100;
              return (
                <>
                  <div>{`${getCurrencyFormat({
                    value: usdProfit,
                    currency: "USD",
                  })} USD`}</div>
                  <div
                    className={
                      profitPercent >= 0 ? "PositiveNumber" : "NegativeNumber"
                    }
                  >{`${getCurrencyFormat({
                    value: profitPercent,
                    currency: "USD",
                  })} %`}</div>
                </>
              );
            }
          },
          id: "bot_profit",
          disableSortBy: true,
        },
        {
          Header: "Total PnL",
          accessor: (originalRow: any, rowIndex: any) => {
            const quoteCurrency = originalRow.pair.split("_")[0];

            if (
              !originalRow.profits ||
              !originalRow.currentInvestments ||
              !originalRow.initialInvestments
            ) {
              return "";
            } else if (investment === "balances" || values === "quote") {
              const quoteProfit = _.sum(
                originalRow.profits.map((profit: any) =>
                  parseFloat(profit.profit)
                )
              );
              const pnl =
                quoteProfit +
                originalRow.currentInvestments.allInQuote -
                originalRow.initialInvestments.allInQuote;
              const pnlPercent =
                (pnl / originalRow.initialInvestments.allInQuote) * 100;
              return (
                <>
                  <div>
                    {`${getCurrencyFormat({
                      value: pnl,
                      currency: quoteCurrency,
                    })} ${quoteCurrency}`}
                  </div>
                  <div
                    className={
                      pnlPercent >= 0 ? "PositiveNumber" : "NegativeNumber"
                    }
                  >{`${getCurrencyFormat({
                    value: pnlPercent,
                    currency: "USD",
                  })} %`}</div>
                </>
              );
            } else {
              const usdProfit = _.sum(
                originalRow.profits.map((profit: any) =>
                  parseFloat(profit.usd_profit)
                )
              );
              const pnl =
                usdProfit +
                originalRow.currentInvestments.allInUsd -
                originalRow.initialInvestments.allInUsd;
              const pnlPercent =
                (pnl / originalRow.initialInvestments.allInUsd) * 100;
              return (
                <>
                  <div>
                    {`${getCurrencyFormat({
                      value: pnl,
                      currency: "USD",
                    })} USD`}
                  </div>
                  <div
                    className={
                      pnlPercent >= 0 ? "PositiveNumber" : "NegativeNumber"
                    }
                  >{`${getCurrencyFormat({
                    value: pnlPercent,
                    currency: "USD",
                  })} %`}</div>
                </>
              );
            }
          },
          id: "total_PnL",
          disableSortBy: true,
        },

        {
          Header: "Stats",
          Cell: ({ cell: { value } }: any) => {
            return (
              <div
                style={{
                  display: "flex",
                  width: "100%",
                  height: "100%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <img className="stats" src={`stats.png`} alt="stats" />
              </div>
            );
          },
          id: "stats",
          disableSortBy: true,
        },
      ] as any,
    [investment, values]
  );

  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } =
    useTable({
      columns,
      data: list.map((gridBotId: number) => gridBots[gridBotId]),
    });

  return (
    <div className="GridBotsListTable">
      <Table hover {...getTableProps()}>
        <thead style={{ fontSize: "14px" }}>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>
                  <div style={{ display: "flex", width: "100%" }}>
                    {column.render("Header")}
                    <div
                      style={{
                        marginLeft: "auto",
                        cursor: column.disableSortBy ? "auto" : "pointer",
                      }}
                    >
                      {column.isSorted
                        ? column.isSortedDesc
                          ? " "
                          : " "
                        : "   "}
                    </div>
                  </div>
                </th>
              ))}
            </tr>
          ))}
        </thead>

        <tbody style={{ fontSize: "14px" }} {...getTableBodyProps()}>
          {rows.map((row: any, i) => {
            prepareRow(row);
            return (
              <tr
                style={{ height: "112px", cursor: "pointer" }}
                {...row.getRowProps()}
                onClick={() => {
                  setModalGridBotId(row.original.id);
                }}
              >
                {row.cells.map((cell: any) => {
                  return (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
};

export default GridBotsListTable;
