import _ from "lodash";
import React, { useContext, useEffect, useState } from "react";
import { Spinner } from "react-bootstrap";
import { GridBotsContext } from "../../../../App";
import { getCurrencyFormat } from "../../../../utils/BotsUtils";
import "./GridBotListMetrics.css";

const GridBotListMetrics = () => {
  const { gridBotsState, gridBotsDispatch } = useContext(GridBotsContext);
  const { list, hasMore, gridBots } = gridBotsState;

  const [totalInvestments, setTotalInvestments] = useState(0);
  const [totalProfits, setTotalProfits] = useState(0);
  const [totaPnL, setTotalPnL] = useState(0);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (!hasMore) {
      const allHaveInvestments = list.every(
        (id: any) => gridBots[id].initialInvestments
      );
      const allHaveCurrentInvestments = list.every(
        (id: any) => gridBots[id].currentInvestments
      );
      const allHaveProfits = list.every((id: any) => gridBots[id].profits);
      if (
        list &&
        allHaveInvestments &&
        allHaveProfits &&
        allHaveCurrentInvestments
      ) {
        const calculatedInitialInvestments = _.sum(
          list.map((id: any) => gridBots[id].initialInvestments.allInUsd)
        );
        const calculatedCurrentInvestments = _.sum(
          list.map((id: any) => gridBots[id].currentInvestments.allInUsd)
        );
        const calculatedProfits = _.sum(
          list.map((id: any) =>
            _.sum(
              gridBots[id].profits.map((profit: any) =>
                parseFloat(profit.usd_profit)
              )
            )
          )
        );

        setIsLoading(false);
        setTotalInvestments(calculatedInitialInvestments);
        setTotalProfits(calculatedProfits);
        setTotalPnL(
          calculatedProfits +
            calculatedCurrentInvestments -
            calculatedInitialInvestments
        );
      }
    }
  }, [list, hasMore, gridBots]);

  return !isLoading ? (
    <div className="GridBotListMetrics">
      <div className="GridBotListMetricsContainer">
        <div className="GridBotListMetric">
          <div className="GridBotListMetricHeader">Total Investment</div>
          <div className="GridBotListMetricBody">{`${getCurrencyFormat({
            value: totalInvestments,
            currency: "USD",
          })} $`}</div>
        </div>
        <div className="GridBotListMetric">
          <div className="GridBotListMetricHeader">Total Bot Profits</div>
          <div
            className={`GridBotListMetricBody ${
              totalProfits >= 0 ? "PositiveNumber" : "NegativeNumber"
            }`}
          >{`${getCurrencyFormat({
            value: totalProfits,
            currency: "USD",
          })} $`}</div>
        </div>
        <div className="GridBotListMetric">
          <div className="GridBotListMetricHeader">Total PnL</div>
          <div
            className={`GridBotListMetricBody ${
              totaPnL >= 0 ? "PositiveNumber" : "NegativeNumber"
            }`}
          >{`${getCurrencyFormat({
            value: totaPnL,
            currency: "USD",
          })} $`}</div>
        </div>
      </div>
    </div>
  ) : (
    <div className="SpinnerContainer">
      <Spinner animation="grow" />
    </div>
  );
};

export default GridBotListMetrics;
