import _ from "lodash";
import moment from "moment";
import React, { useContext, useEffect, useState } from "react";
import { Table } from "react-bootstrap";
import { GridBotsContext } from "../../../../App";
import { getCurrencyFormat } from "../../../../utils/BotsUtils";
import "./GridBotDetailsTable.css";

const GridBotDetailsTable = ({
  gridBotId,
  profits,
  marketOrders,
  tradingFee,
  timeRangeValue,
}: any) => {
  const { gridBotsState } = useContext(GridBotsContext);

  const { gridBots } = gridBotsState;

  const gridBot = gridBots[gridBotId];

  const quoteCurrency = gridBot.pair.split("_")[0];

  const [profit, setProfit] = useState(0);
  const [avgProfit, setAvgProfit] = useState(0);
  const [returns, setReturns] = useState(0);
  const [running, setRunning] = useState(0);

  useEffect(() => {
    const summarizedProfits = _.sum(
      profits.map((profit: any) => parseFloat(profit.profit))
    );
    const summarizedMarketOrders =
      _.sum(
        marketOrders.map((marketOrder: any) => parseFloat(marketOrder.total))
      ) *
      tradingFee *
      0.01;
    const realProfit = summarizedProfits - summarizedMarketOrders;

    let period = 1;

    if (timeRangeValue === "30d") {
      period = running > 30 ? 30 : running;
    } else {
      period = running;
    }

    const realAvgProfit = period ? realProfit / period : 0;
    const quoteInvestment = gridBot.initialInvestments
      ? gridBot.initialInvestments.allInQuote
      : 0;
    const realReturns = quoteInvestment
      ? (realProfit * 100) / quoteInvestment
      : 0;

    const start = moment.utc(gridBot.created_at);
    const end = gridBot.closed_at
      ? moment.utc(gridBot.closed_at)
      : moment.utc();

    setProfit(realProfit);
    setAvgProfit(realAvgProfit);
    setReturns(realReturns);
    setRunning(end.diff(start, "days"));
  }, [gridBot, profits, marketOrders, tradingFee, timeRangeValue]);

  return (
    profits &&
    marketOrders && (
      <Table hover>
        <thead>
          <tr>
            <th>Days running</th>
            <th>Transactions</th>
            <th>Avg. daily</th>
            <th>Bot profit</th>
            <th>Returns</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{running}</td>
            <td>{profits ? profits.length : ""}</td>
            <td>{`${getCurrencyFormat({
              value: avgProfit,
              currency: quoteCurrency,
            })} ${quoteCurrency}`}</td>
            <td>{`${getCurrencyFormat({
              value: profit,
              currency: quoteCurrency,
            })} ${quoteCurrency}`}</td>
            <td>{`${getCurrencyFormat({
              value: returns,
              currency: "USD",
            })} %`}</td>
          </tr>
        </tbody>
      </Table>
    )
  );
};

export default GridBotDetailsTable;
